@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
  }
}
@keyframes enter {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes leave {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.9);
  }
}
.animate-enter {
  animation: enter 0.3s ease-out forwards;
}
.animate-leave {
  animation: leave 0.3s ease-in forwards;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}
@keyframes glow {
  from {
    box-shadow: 0 0 20px rgba(69, 59, 207, 0.4);
  }
  to {
    box-shadow: 0 0 30px rgba(69, 59, 207, 0.8);
  }
}
* {
  transition: background-color 0.2s ease,
    color 0.2s ease,
    border-color 0.2s ease,
    opacity 0.2s ease;
}
.backdrop-blur-enhanced {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}
.gradient-primary {
  background: linear-gradient(135deg, #453bcf 0%, #6366f1 100%);
}
.gradient-secondary {
  background: linear-gradient(135deg, #f8f9fa 0%, #e5e7eb 100%);
}
.dark .gradient-secondary {
  background: linear-gradient(135deg, #374151 0%, #4b5563 100%);
}
.shadow-enhanced {
  box-shadow: 0 10px 25px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.dark .shadow-enhanced {
  box-shadow: 0 10px 25px -3px rgba(0, 0, 0, 0.4),
    0 4px 6px -2px rgba(0, 0, 0, 0.1);
}
.text-gradient-primary {
  background: linear-gradient(135deg, #453bcf, #6366f1);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.text-gradient-neutral {
  background: linear-gradient(135deg, #1f2937, #6b7280);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.dark .text-gradient-neutral {
  background: linear-gradient(135deg, #f9fafb, #d1d5db);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card-glass {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.dark .card-glass {
  background: rgba(17, 24, 39, 0.8);
  border: 1px solid rgba(75, 85, 99, 0.3);
}
.hover-lift {
  transition: transform 0.3s ease,
    box-shadow 0.3s ease;
}
.hover-lift:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px -12px rgba(0, 0, 0, 0.15);
}
.dark .hover-lift:hover {
  box-shadow: 0 20px 40px -12px rgba(0, 0, 0, 0.4);
}
.overflow-y-snap {
  max-height: 70vh;
  overflow-y: auto;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
}
.overflow-y-snap > * {
  scroll-snap-align: start;
}
::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
::-webkit-scrollbar-track {
  background: #f0f0f0;
  border-radius: 2px;
}
::-webkit-scrollbar-thumb {
  background-color: #f30000;
  border-radius: 2px;
  border: 1px solid #f0f0f0;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #c00000;
}
